let liElements = [];
images.forEach(elem => {
    let li = document.createElement('li');
    li.classList = 'gallery-item';

    let link = document.createElement('a');
    link.classList = 'gallery-link';
    link.href = elem.original;
    link.addEventListener("click", (e) => {
        e.preventDefault();
    })

    let img = document.createElement('img');
    img.classList = 'gallery-image';
    img.src = elem.preview;
    img.alt = elem.description;

    link.append(img);
    li.append(link);
    liElements.push(li)
})

document.querySelector(".gallery").append(...liElements);

let gallery = new SimpleLightbox('.gallery a', {
    captionDelay: 250,
    captions: true,
    captionSelector: 'img',
    captionsData: "alt",
    captionPosition: 'bottom',
    animated: true
});